import Vue from "vue";
// console.log(Vue.axios);
// console.log(axios);

//  根据日期获取每日推荐
const getRecommend = (date = 0) =>
    Vue.axios.get("https://apis.netstart.cn/one/channel/one/" + date);

//  根据年份获取连载列表
const getSerialList = (year = 0) =>
    Vue.axios.get("https://apis.netstart.cn/one/find/serial/byyear/" + year);

//  根据连载的id获取章节目录
const FoundSerialList = (serialId) =>
    Vue.axios.get("https://apis.netstart.cn/one/find/serial/list/" + serialId);

//  根据月份获取当月所有日期
const getEveryDays = (date) =>
    Vue.axios.get("https://apis.netstart.cn/one/feeds/list/" + date);

//  根据类型及文章id获取文章详情
const getArticles = (type, articalId) =>
    Vue.axios.get("https://apis.netstart.cn/one/" + type + "/htmlcontent/" + articalId);

//  根据类型及文章id及评论id获取文章评论
const getComments = (type, articalId, commendId = 0) =>
    Vue.axios.get("https://apis.netstart.cn/one/comment/praiseandtime/" + type + "/" + articalId + "/" + commendId);

//  根据类型和月份获取发现页文章列表
const getFoundArticle = (type, date = '2022-10') =>
    Vue.axios.get("https://apis.netstart.cn/one/find/bymonth/" + type + "/" + date);

//  获取发现页专题列表
const getFoundProject = () =>
    Vue.axios.get("https://apis.netstart.cn/one/banner/list/4");

//  根据id获取发现页小记列表
const getFoundDiary = (diaryId = 0) =>
    Vue.axios.get("https://apis.netstart.cn/one/diary/square/more/" + diaryId);

//  获取发现页所有榜单列表
const getFoundRank = () =>
    Vue.axios.get("https://apis.netstart.cn/one/find/rank");

//  根据id获取发现页榜单详情
const getFoundRankDetail = (rankId = 7) =>
    Vue.axios.get("https://apis.netstart.cn/one/find/rank/" + rankId);

//  获取发现页热门作者
const getFoundAuthor = () =>
    Vue.axios.get("https://apis.netstart.cn/one/author/hot");

//  获取发现页热门作者
const getSearchResult = (categoryName, keyword) =>
    Vue.axios.get("https://apis.netstart.cn/one/search/" + categoryName + "/" + keyword + "/0");

//  获取发现页热门作者
const getGraphicDetails = (date) =>
    Vue.axios.get("https://apis.netstart.cn/one/hp/bydate/" + date);

//  获取发现页热门作者详情
const getAuthorDetails = (id, page = 0) =>
    Vue.axios.get("https://apis.netstart.cn/one/author/works?", {
        params: {
            author_id: id,
            page_num: page,
        }
    });

export { getRecommend, getSerialList, FoundSerialList, getEveryDays, getArticles, getComments, getFoundArticle, getFoundProject, getFoundDiary, getFoundRank, getFoundRankDetail, getFoundAuthor, getSearchResult, getGraphicDetails, getAuthorDetails };
